@import-normalize;
.header {
  display: grid;
  grid-template-columns: 80px 1fr 80px; }
  .header .header__logo {
    grid-column: 2 / 3;
    max-width: 100%;
    height: auto; }

.giphy {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .giphy .search {
    display: flex; }

.search__field {
  height: 40px;
  border: 2px solid #913BF1;
  font-size: 1.2rem;
  border-right: none;
  outline: none;
  width: 800px;
  padding-left: 10px; }
  .search__field::placeholder {
    font-size: 0.9rem; }

.search__button {
  width: 45px;
  color: white;
  outline: none;
  font-size: 1.2rem;
  border: 2px solid #913BF1;
  background: #a943bd;
  cursor: pointer;
  background: linear-gradient(229deg, #a943bd 2%, #913bf1 100%); }

.footer .footer__copyright {
  text-align: center;
  color: #777986; }

.footer a, .footer a:link, .footer a:visited, .footer a:active {
  color: #913BF1; }
